<template>
	<view class="container">
		<!-- Êä•ÂëäÂ§¥ÈÉ® -->
		<view class="report-header">
			<view class="header-title">ÂèëËÇ≤ËØÑ‰º∞Êä•Âëä</view>
			<view class="child-info">
				<text class="child-name">{{ assessmentResult.childInfo.name }}</text>
				<text class="child-age">{{ calculatedAge }}</text>
			</view>
			<view class="assessment-date">
				ËØÑ‰º∞Êó•ÊúüÔºö{{ formatDate(assessmentResult.assessmentDate) }}
			</view>
		</view>
		
		<!-- ÊÄª‰ΩìËØÑÂàÜ -->
		<view class="overall-score">
			<view class="score-circle">
				<view class="score-number">{{ Math.round(assessmentResult.scorePercentage) }}</view>
				<view class="score-label">ÁªºÂêàËØÑÂàÜ</view>
			</view>
			<view class="score-description">
				<view class="score-level" :class="getScoreLevelClass()">
					{{ getScoreLevel() }}
				</view>
				<view class="score-text">{{ getScoreDescription() }}</view>
			</view>
		</view>
		
		<!-- ÂêÑÁª¥Â∫¶ËØÑÂàÜ -->
		<view class="dimension-scores">
			<view class="section-title">ÂêÑÁª¥Â∫¶ËØÑÂàÜ</view>
			<view class="dimension-list">
				<view 
					class="dimension-item" 
					v-for="(dimension, index) in dimensionScores" 
					:key="index"
				>
					<view class="dimension-header">
						<view class="dimension-icon">{{ dimension.icon }}</view>
						<view class="dimension-name">{{ dimension.name }}</view>
						<view class="dimension-score">{{ dimension.score }}/{{ dimension.maxScore }}</view>
					</view>
					<view class="dimension-bar">
						<view 
							class="dimension-fill" 
							:style="{ width: dimension.percentage + '%' }"
							:class="getDimensionClass(dimension.percentage)"
						></view>
					</view>
					<view class="dimension-description">{{ dimension.description }}</view>
				</view>
			</view>
		</view>
		
		<!-- ËØ¶ÁªÜÂàÜÊûê -->
		<view class="detailed-analysis">
			<view class="section-title">ËØ¶ÁªÜÂàÜÊûê</view>
			<view class="analysis-content">
				<view class="analysis-item" v-for="(item, index) in detailedAnalysis" :key="index">
					<view class="analysis-title">{{ item.title }}</view>
					<view class="analysis-text">{{ item.content }}</view>
				</view>
			</view>
		</view>
		
		<!-- Âª∫ËÆÆ‰∏éÊåáÂØº -->
		<view class="recommendations">
			<view class="section-title">Âª∫ËÆÆ‰∏éÊåáÂØº</view>
			<view class="recommendation-list">
				<view 
					class="recommendation-item" 
					v-for="(rec, index) in recommendations" 
					:key="index"
				>
					<view class="rec-icon">{{ rec.icon }}</view>
					<view class="rec-content">
						<view class="rec-title">{{ rec.title }}</view>
						<view class="rec-text">{{ rec.content }}</view>
					</view>
				</view>
			</view>
		</view>
		
		<!-- Â∫ïÈÉ®Êìç‰Ωú -->
		<view class="footer-actions">
			<button class="btn-secondary" @click="viewHistory">Êü•ÁúãÂéÜÂè≤</button>
			<button class="btn-primary" @click="newAssessment">ÈáçÊñ∞ËØÑ‰º∞</button>
			<button class="btn-success" @click="shareReport">ÂàÜ‰∫´Êä•Âëä</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				assessmentResult: {},
				dimensionScores: [],
				detailedAnalysis: [],
				recommendations: []
			}
		},
		computed: {
			// ËÆ°ÁÆóÂπ¥ÈæÑ
			calculatedAge() {
				if (!this.assessmentResult.childInfo || !this.assessmentResult.childInfo.birthDate) return '';
				const birth = new Date(this.assessmentResult.childInfo.birthDate);
				const today = new Date();
				const ageInMonths = (today.getFullYear() - birth.getFullYear()) * 12 + 
								   (today.getMonth() - birth.getMonth());
				
				if (ageInMonths < 12) {
					return `${ageInMonths}‰∏™Êúà`;
				} else {
					const years = Math.floor(ageInMonths / 12);
					const months = ageInMonths % 12;
					return months > 0 ? `${years}Â≤Å${months}‰∏™Êúà` : `${years}Â≤Å`;
				}
			}
		},
		onLoad() {
			// Ëé∑ÂèñËØÑ‰º∞ÁªìÊûú
			this.assessmentResult = uni.getStorageSync('assessmentResult') || {};
			
			if (!this.assessmentResult.answers) {
				uni.showToast({
					title: 'Êú™ÊâæÂà∞ËØÑ‰º∞ÁªìÊûú',
					icon: 'none'
				});
				setTimeout(() => {
					uni.navigateBack();
				}, 1500);
				return;
			}
			
			// ËÆ°ÁÆóÂêÑÁª¥Â∫¶ËØÑÂàÜ
			this.calculateDimensionScores();
			
			// ÁîüÊàêËØ¶ÁªÜÂàÜÊûê
			this.generateDetailedAnalysis();
			
			// ÁîüÊàêÂª∫ËÆÆ
			this.generateRecommendations();
		},
		methods: {
			// ËÆ°ÁÆóÂêÑÁª¥Â∫¶ËØÑÂàÜ
			calculateDimensionScores() {
				const dimensions = {
					motor: { name: 'ËøêÂä®ÂèëËÇ≤', icon: 'üèÉ', maxScore: 6 },
					language: { name: 'ËØ≠Ë®ÄÂèëËÇ≤', icon: 'üó£Ô∏è', maxScore: 6 },
					cognitive: { name: 'ËÆ§Áü•ÂèëËÇ≤', icon: 'üß†', maxScore: 6 },
					social: { name: 'Á§æ‰∫§ÂèëËÇ≤', icon: 'üë•', maxScore: 6 }
				};
				
				this.dimensionScores = [];
				const questionsPerType = 2;
				
				this.assessmentResult.assessmentTypes.forEach((type, typeIndex) => {
					const startIndex = typeIndex * questionsPerType;
					const endIndex = startIndex + questionsPerType;
					const typeAnswers = this.assessmentResult.answers.slice(startIndex, endIndex);
					const score = typeAnswers.reduce((sum, answer) => sum + answer.score, 0);
					const percentage = (score / dimensions[type].maxScore) * 100;
					
					this.dimensionScores.push({
						name: dimensions[type].name,
						icon: dimensions[type].icon,
						score: score,
						maxScore: dimensions[type].maxScore,
						percentage: percentage,
						description: this.getDimensionDescription(percentage)
					});
				});
			},
			
			// Ëé∑ÂèñÁª¥Â∫¶ÊèèËø∞
			getDimensionDescription(percentage) {
				if (percentage >= 80) return 'ÂèëËÇ≤ËâØÂ•ΩÔºåÁªßÁª≠‰øùÊåÅ';
				if (percentage >= 60) return 'ÂèëËÇ≤Ê≠£Â∏∏ÔºåÊúâÊèêÂçáÁ©∫Èó¥';
				if (percentage >= 40) return 'ÂèëËÇ≤Á®çÊÖ¢ÔºåÈúÄË¶ÅÂÖ≥Ê≥®';
				return 'ÂèëËÇ≤ËæÉÊÖ¢ÔºåÂª∫ËÆÆÂí®ËØ¢‰∏ì‰∏öÂåªÁîü';
			},
			
			// ÁîüÊàêËØ¶ÁªÜÂàÜÊûê
			generateDetailedAnalysis() {
				this.detailedAnalysis = [
					{
						title: 'Êï¥‰ΩìÂèëËÇ≤Ê∞¥Âπ≥',
						content: this.getOverallAnalysis()
					},
					{
						title: '‰ºòÂäøÈ¢ÜÂüü',
						content: this.getStrengthsAnalysis()
					},
					{
						title: 'ÈúÄË¶ÅÂÖ≥Ê≥®ÁöÑÈ¢ÜÂüü',
						content: this.getConcernsAnalysis()
					}
				];
			},
			
			// Ëé∑ÂèñÊï¥‰ΩìÂàÜÊûê
			getOverallAnalysis() {
				const percentage = this.assessmentResult.scorePercentage;
				if (percentage >= 80) {
					return 'ÊÇ®ÁöÑÂ≠©Â≠êÊï¥‰ΩìÂèëËÇ≤Ê∞¥Âπ≥ËâØÂ•ΩÔºåÂêÑÈ°πËÉΩÂäõÂèëÂ±ïÂùáË°°ÔºåÁ¨¶ÂêàËØ•Âπ¥ÈæÑÊÆµÁöÑÂèëÂ±ïÊ†áÂáÜ„ÄÇ';
				} else if (percentage >= 60) {
					return 'ÊÇ®ÁöÑÂ≠©Â≠êÂèëËÇ≤Ê∞¥Âπ≥Ê≠£Â∏∏ÔºåÂ§ßÈÉ®ÂàÜËÉΩÂäõÂèëÂ±ïËâØÂ•ΩÔºå‰∏™Âà´ÊñπÈù¢ÊúâÊèêÂçáÁ©∫Èó¥„ÄÇ';
				} else if (percentage >= 40) {
					return 'ÊÇ®ÁöÑÂ≠©Â≠êÂèëËÇ≤Ê∞¥Âπ≥Á®çÊÖ¢ÔºåÂª∫ËÆÆÂä†Âº∫Áõ∏ÂÖ≥ËÆ≠ÁªÉÔºåÂÆöÊúüËøõË°åÂèëËÇ≤ÁõëÊµã„ÄÇ';
				} else {
					return 'ÊÇ®ÁöÑÂ≠©Â≠êÂèëËÇ≤Ê∞¥Âπ≥ËæÉÊÖ¢ÔºåÂª∫ËÆÆÂèäÊó∂Âí®ËØ¢‰∏ì‰∏öÂåªÁîüÔºåËøõË°åËØ¶ÁªÜÁöÑÂèëËÇ≤ËØÑ‰º∞„ÄÇ';
				}
			},
			
			// Ëé∑Âèñ‰ºòÂäøÂàÜÊûê
			getStrengthsAnalysis() {
				const strengths = this.dimensionScores
					.filter(d => d.percentage >= 70)
					.map(d => d.name);
				
				if (strengths.length > 0) {
					return `ÊÇ®ÁöÑÂ≠©Â≠êÂú®${strengths.join('„ÄÅ')}ÊñπÈù¢Ë°®Áé∞‰ºòÁßÄÔºåËøôÊòØÂæàÂ•ΩÁöÑÂèëÂ±ïÂü∫Á°Ä„ÄÇ`;
				}
				return 'Âª∫ËÆÆÁªßÁª≠ËßÇÂØüÂíåÂüπÂÖªÂ≠©Â≠êÁöÑÂêÑÈ°πËÉΩÂäõÔºåÂèëÁé∞ÂÖ∂ÂÖ¥Ë∂£ÂíåÁâπÈïø„ÄÇ';
			},
			
			// Ëé∑ÂèñÂÖ≥Ê≥®ÁÇπÂàÜÊûê
			getConcernsAnalysis() {
				const concerns = this.dimensionScores
					.filter(d => d.percentage < 60)
					.map(d => d.name);
				
				if (concerns.length > 0) {
					return `Âª∫ËÆÆÈáçÁÇπÂÖ≥Ê≥®${concerns.join('„ÄÅ')}ÊñπÈù¢ÁöÑÂèëÂ±ïÔºåÂèØ‰ª•ÈÄöËøáÊ∏∏ÊàèÂíåËÆ≠ÁªÉÊù•‰øÉËøõÁõ∏ÂÖ≥ËÉΩÂäõÁöÑÊèêÂçá„ÄÇ`;
				}
				return 'ÊÇ®ÁöÑÂ≠©Â≠êÂêÑÈ°πËÉΩÂäõÂèëÂ±ïÂùáË°°ÔºåÁªßÁª≠‰øùÊåÅËâØÂ•ΩÁöÑÂèëÂ±ïÊÄÅÂäø„ÄÇ';
			},
			
			// ÁîüÊàêÂª∫ËÆÆ
			generateRecommendations() {
				this.recommendations = [
					{
						icon: 'üìö',
						title: 'Êó•Â∏∏ËÆ≠ÁªÉÂª∫ËÆÆ',
						content: 'Ê†πÊçÆËØÑ‰º∞ÁªìÊûúÔºåÂª∫ËÆÆÂú®Êó•Â∏∏ÁîüÊ¥ª‰∏≠Â§öËøõË°åÁõ∏ÂÖ≥ËÉΩÂäõÁöÑËÆ≠ÁªÉÔºåÂ¶ÇÊ∏∏Êàè„ÄÅÈòÖËØª„ÄÅËøêÂä®Á≠â„ÄÇ'
					},
					{
						icon: 'üë®‚Äç‚öïÔ∏è',
						title: '‰∏ì‰∏öÂí®ËØ¢',
						content: 'Âª∫ËÆÆÂÆöÊúüÂí®ËØ¢ÂÑøÁßëÂåªÁîüÊàñÂèëËÇ≤‰∏ìÂÆ∂ÔºåËé∑ÂæóÊõ¥‰∏ì‰∏öÁöÑÊåáÂØºÂíåÂª∫ËÆÆ„ÄÇ'
					},
					{
						icon: 'üìä',
						title: 'ÂÆöÊúüËØÑ‰º∞',
						content: 'Âª∫ËÆÆÊØè3-6‰∏™ÊúàËøõË°å‰∏ÄÊ¨°ÂèëËÇ≤ËØÑ‰º∞ÔºåË∑üË∏™Â≠©Â≠êÁöÑÊàêÈïøÂèòÂåñ„ÄÇ'
					},
					{
						icon: 'üë®‚Äçüë©‚Äçüëß‚Äçüë¶',
						title: 'ÂÆ∂Â∫≠ÊîØÊåÅ',
						content: 'ÂÆ∂ÈïøÁöÑÊîØÊåÅÂíåÈºìÂä±ÂØπÂ≠©Â≠êÁöÑÂèëËÇ≤ÈùûÂ∏∏ÈáçË¶ÅÔºåËØ∑‰øùÊåÅËÄêÂøÉÂíåÁßØÊûÅÁöÑÊÄÅÂ∫¶„ÄÇ'
					}
				];
			},
			
			// Ëé∑ÂèñËØÑÂàÜÁ≠âÁ∫ß
			getScoreLevel() {
				const percentage = this.assessmentResult.scorePercentage;
				if (percentage >= 80) return '‰ºòÁßÄ';
				if (percentage >= 60) return 'ËâØÂ•Ω';
				if (percentage >= 40) return '‰∏ÄËà¨';
				return 'ÈúÄË¶ÅÂÖ≥Ê≥®';
			},
			
			// Ëé∑ÂèñËØÑÂàÜÁ≠âÁ∫ßÊ†∑ÂºèÁ±ª
			getScoreLevelClass() {
				const percentage = this.assessmentResult.scorePercentage;
				if (percentage >= 80) return 'excellent';
				if (percentage >= 60) return 'good';
				if (percentage >= 40) return 'average';
				return 'concern';
			},
			
			// Ëé∑ÂèñËØÑÂàÜÊèèËø∞
			getScoreDescription() {
				const percentage = this.assessmentResult.scorePercentage;
				if (percentage >= 80) return 'ÂèëËÇ≤Ê∞¥Âπ≥‰ºòÁßÄÔºåÂêÑÈ°πËÉΩÂäõÂèëÂ±ïËâØÂ•Ω';
				if (percentage >= 60) return 'ÂèëËÇ≤Ê∞¥Âπ≥ËâØÂ•ΩÔºåÁ¨¶ÂêàÂπ¥ÈæÑÊ†áÂáÜ';
				if (percentage >= 40) return 'ÂèëËÇ≤Ê∞¥Âπ≥‰∏ÄËà¨ÔºåÊúâÊèêÂçáÁ©∫Èó¥';
				return 'ÂèëËÇ≤Ê∞¥Âπ≥ÈúÄË¶ÅÂÖ≥Ê≥®ÔºåÂª∫ËÆÆ‰∏ì‰∏öÂí®ËØ¢';
			},
			
			// Ëé∑ÂèñÁª¥Â∫¶Ê†∑ÂºèÁ±ª
			getDimensionClass(percentage) {
				if (percentage >= 80) return 'excellent';
				if (percentage >= 60) return 'good';
				if (percentage >= 40) return 'average';
				return 'concern';
			},
			
			// Ê†ºÂºèÂåñÊó•Êúü
			formatDate(dateString) {
				const date = new Date(dateString);
				return `${date.getFullYear()}Âπ¥${date.getMonth() + 1}Êúà${date.getDate()}Êó•`;
			},
			
			// Êü•ÁúãÂéÜÂè≤ËÆ∞ÂΩï
			viewHistory() {
				uni.navigateTo({
					url: '/pages/history/history'
				});
			},
			
			// ÈáçÊñ∞ËØÑ‰º∞
			newAssessment() {
				uni.showModal({
					title: 'ÈáçÊñ∞ËØÑ‰º∞',
					content: 'Á°ÆÂÆöË¶ÅÂºÄÂßãÊñ∞ÁöÑËØÑ‰º∞ÂêóÔºü',
					success: (res) => {
						if (res.confirm) {
							uni.navigateTo({
								url: '/pages/child-info/child-info'
							});
						}
					}
				});
			},
			
			// ÂàÜ‰∫´Êä•Âëä
			shareReport() {
				uni.showToast({
					title: 'ÂàÜ‰∫´ÂäüËÉΩÂºÄÂèë‰∏≠',
					icon: 'none'
				});
			}
		}
	}
</script>

